import publicHead from "@/components/headBack/head.vue";
import contractText from "@/assets/script/contract";
import dowwnTime from "@/components/downTime/downtime.vue";
export default{
    data() {
        return {
            // lp余额
            userBalance: 0,
            // 质押金额
            pledgeNum: 0,
            // 授权金额
            authNum: 0,
            // 当前奖励
            currReward: 0,
            // 已领取奖励
            isGetNum: 0,
            // 是否显示质押输入框
            showPledgeFloat: false,
            // 质押输入框类型，1质押 2提取
            showPledfeFloatType: 1,
            // 输入的数量
            inputNum: "",
            // 显示的lodingUI
            loadingObj: null,
            // 保存计时器
            timeOutObj: null,
            // 是否显示倒计时
            donwTime: new Date("2023/01/01 00:00:00").getTime() / 1000 +  ' '
        }
    },

    mounted() {
        var _this = this;
        this.loadingObj = this.$createToast({
            time: 0,
            mask: true,
            txt: "Loading",
            type: "loading",
        });
        this.getUserMoneyMsg();
        this.getUserReward();
    },

    beforeDestroy() {
        clearTimeout(this.timeOutObj);
    },

    methods: {
        // 获取用户授权、质押金额
        getUserMoneyMsg() {
            // 农场合约对象
            var contract1 = this.getContract("FarmingPool");
            // erc20合约对象
            var contract2 = this.getContract("LpPair");
            var _this = this;
            contract1.methods.userInfo(this.$store.state.dpi,this.$store.state.addres).call().then((res)=>{
                _this.pledgeNum = _this.accuracyDiv(res.amount);
            });
            contract2.methods.allowance(this.$store.state.addres,contractText['FarmingPool'].addres).call().then((res)=>{
                _this.authNum = res;
            });
            contract2.methods.balanceOf(this.$store.state.addres).call().then((res)=>{
                _this.userBalance = _this.accuracyDiv( res);
            });
        },

        // 获取用户质押奖励
        getUserReward() {
            var contractObj = this.getContract("FarmingPool");
            var _this = this;
            contractObj.methods.pending(this.$store.state.dpi,this.$store.state.addres).call().then((res)=>{
                console.log('实时更新');
                _this.currReward = _this.accuracyDiv( res);
            });
            _this.timeOutObj = setTimeout(()=>{
                _this.getUserReward();
            },3000);
        },

        // 弹出成OK按钮执行函数
        floatConfirmAction() {
            if(this.showPledfeFloatType == 1) {
                this.confirmPledge();
            }else{
                this.confirmWithdrawalbut();
            }
        },
        
        // 点击质押按钮执行函数
        pledgeButAction() {
            if(this.userBalance == 0) {
                this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'warn',
                    txt: this.$t("m.text116"),
                }).show();
                return;
            }
            this.showPledfeFloatType = 1;
            this.showPledgeFloat = true;
        },

        // 确认质押
        async confirmPledge() {
            if(!this.inputNum) {
                this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'warn',
                    txt: this.$t("m.text117"),
                }).show();
                return;
            }
            var _this = this;
            this.loadingObj.show();
            if(this.inputNum > this.authNum) {
                await this.approveAction(this.inputNum);
                this.pledgeAction(this.inputNum);
            }else{
                this.pledgeAction(this.inputNum);
            }
        },

        // 授权执行函数
        approveAction(money) {
            var contractObj = this.getContract("LpPair");
            var _this = this;
            return new Promise((resolve,reject) => {
                var approve = contractObj.methods.approve(contractText["FarmingPool"].addres,this.accuracyMul(money));
                approve.send({
                    from: _this.$store.state.addres
                }).then((res)=>{
                    if(res.status) {
                        _this.getUserMoneyMsg();
                        resolve();
                    }else{
                        reject();
                        _this.loadingObj.hide();
                        _this.$createToast({
                            time: 1500,
                            mask: true,
                            type: 'error',
                            txt: _this.$t("m.text68"),
                        }).show();
                    }
                }).catch(()=>{
                    reject();
                    _this.loadingObj.hide();
                    _this.$createToast({
                        time: 1500,
                        mask: true,
                        type: 'error',
                        txt: _this.$t("m.text68"),
                    }).show();
                });
            });
        },

        //质押执行函数
        pledgeAction(money) {
            var contractObj = this.getContract("FarmingPool");
            var _this = this;
            if(money == 0) {
                this.loadingObj.show();
            }
            var deposit = contractObj.methods.deposit(this.$store.state.dpi,this.accuracyMul(money));
            deposit.send({
                from: this.$store.state.addres,
                value: 0
            }).then((res)=>{
                _this.loadingObj.hide();
                _this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'correct',
                    txt: _this.$t("m.text67"),
                }).show();
                _this.getUserMoneyMsg();
                _this.closeFloat();
            }).catch(()=>{
                _this.loadingObj.hide();
                _this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'error',
                    txt: _this.$t("m.text68"),
                }).show();
            })
        },

        // 提取按钮执行函数
        withdrawalButAction() {
            if(this.pledgeNum == 0) {
                this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'warn',
                    txt: this.$t("m.text116"),
                }).show();
                return;
            }
            this.showPledfeFloatType = 2;
            this.showPledgeFloat = true;
        },

        // 确认提取执行函数
        confirmWithdrawalbut() {
            if(!this.inputNum) {
                this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'warn',
                    txt: this.$t("m.text204"),
                }).show();
                return;
            }
            this.loadingObj.show();
            var _this = this;
            var contractObj = this.getContract("FarmingPool");
            var withdraw = contractObj.methods.withdraw(this.$store.state.dpi,this.accuracyMul(this.inputNum));
            withdraw.send({
                from: this.$store.state.addres,
                value: 0,
            }).then((res)=>{
                _this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'correct',
                    txt: _this.$t("m.text67"),
                }).show();
                _this.getUserMoneyMsg();
                _this.closeFloat();
            }).catch((error)=>{
                console.log(error);
                _this.loadingObj.hide();
                this.$createToast({
                    time: 1500,
                    mask: true,
                    type: 'warn',
                    txt: this.$t("m.text68"),
                }).show();
            });
        },

        // 余额全押
        allIn() {
            if(this.showPledfeFloatType == 1) {
                this.inputNum = this.userBalance;
            }else{
                this.inputNum = this.pledgeNum;
            }
        },

        // 关闭浮动窗口
        closeFloat() {
            this.inputNum = "";
            this.showPledgeFloat = false;
        },

        // 前往提问
        goToQuest() {
            this.$router.push({
                path: "/question"
            })
        },

        // 前往农场
        goToComputer() {
            this.$router.push({
                path: "/calculator"
            })
        }
    },
    components: {
        publicHead,
        dowwnTime
    }
}